.Title {
    white-space: pre-line;
    font-size: 60px;
    justify-content: center;
    text-align: center;
    text-shadow: 10px;
  }

.subTitle {
    white-space: pre-line;
    font-size: 40px;
    justify-content: center;
    text-align: center;
    text-shadow: 10px;
    font-family: 'Oswald', sans-serif;
    
  }


 .playerImage {
    border-radius: 8px;
    width: 100%;
    height: auto;
    max-width: 500px;
    margin-right: auto;
    margin-left: auto;
    display: block;
    padding-top: 10px;
 }

 .basicPlayerInfo {
     width: auto;
     height: auto;
 }

